<template>
  <div>
    <v-card max-width="1000px" class="mx-auto mt-5" v-if="temHorarios">
      <v-toolbar flat>
        <v-toolbar-title
          >Alterações na estrutura bloqueadas, pois a configuração já tem grades
          geradas</v-toolbar-title
        >
        <v-spacer></v-spacer>
        <v-btn color="primary" dark class="mb-2" @click="$emit('navigate', 7)">
          Gerenciar grades
        </v-btn>
      </v-toolbar>
    </v-card>
    <v-card max-width="1000px" class="mx-auto mt-5">
      <v-toolbar flat>
        <v-toolbar-title>Observações</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-btn
          color="primary"
          dark
          class="mb-2"
          v-if="observacao == null"
          @click="observacao = ''"
        >
          Adicionar observação
        </v-btn>
      </v-toolbar>
      <v-card-text v-if="observacao != null">
        <v-textarea
          label="Observações"
          v-model="observacao"
          auto-grow
          clearable
          rows="1"
          @change="changed"
          spellcheck="false"
        >
        </v-textarea>
      </v-card-text>
    </v-card>
    <tabela-materias
      :items="materias"
      @delete="deleteMateria"
      @save="saveMateria"
      :locked="temHorarios"
      :materias="materias"
    />
    <tabela-professores
      :items="professores"
      :numero-dias="numero_dias"
      @delete="deleteProfessor"
      @save="saveProfessor"
      :locked="temHorarios"
    />
    <tabela-turnos
      :items="turnos"
      @delete="deleteTurno"
      @save="saveTurno"
      :locked="temHorarios"
    />
    <v-card max-width="1000px" class="mx-auto mt-5">
      <tabela-salas
        :_turnos="turnos"
        :_salas="salas"
        :turnoSelecionado="turnoSelecionado"
        @delete="deleteSala"
        @save="saveSala"
        :locked="temHorarios"
      />
    </v-card>
  </div>
</template>

<script>
import { v4 as uuidv4, validate as isUuid } from "uuid";
import helper from "@/mixins/helper";

export default {
  name: "ConfiguracoesHorario",
  mixins: [helper],
  components: {
    TabelaTurnos: () => import("@/components/TabelaTurnos.vue"),
    TabelaProfessores: () => import("@/components/TabelaProfessores.vue"),
    TabelaSalas: () => import("@/components/TabelaSalas.vue"),
    TabelaMaterias: () => import("@/components/TabelaMaterias.vue"),
  },
  props: ["id"],
  data() {
    return {
      turnos: [],
      salas: [],
      materias: [],
      professores: [],
      tabelasReload: 0,
      temHorarios: false,
      observacao: null,
    };
  },

  watch: {
    turnos() {
      if (this.turnoSelecionado == null && this.turnos.length > 0) {
        this.turnoSelecionado = this.turnos[0].id;
      }
    },
    observacao(newValue, oldValue) {
      if (newValue == null && oldValue != null) {
        this.changed();
      }
    },
  },

  mounted() {
    this.load();
  },

  methods: {
    async load(isReload = false) {
      if (!this.id) {
        return;
      }
      try {
        this.setLoading(isReload);
        this.saved();
        const resp = await this.$axios.get("/configuracao/" + this.id);
        const {
          turnos,
          salas,
          professores,
          materias,
          configuracao,
          temHorarios,
        } = resp.data;
        this.turnos = turnos;
        this.salas = salas;
        this.professores = professores;
        this.materias = materias;
        this.numero_dias = configuracao.numero_dias;
        this.temHorarios = temHorarios;
        this.observacao = resp.data.configuracao.observacao;

        if (
          turnos != null &&
          turnos.length > 0 &&
          (this.turnoSelecionado == null || isUuid(this.turnoSelecionado))
        ) {
          this.turnoSelecionado = turnos[0].id;
        }
      } catch (e) {
        console.error(e);
      } finally {
        this.clearLoading();
      }
    },

    save(reload = true) {
      const { turnos, salas, professores, materias } = this;

      let observacao = this.observacao != null ? this.observacao.trim() : null;
      if (observacao != null && observacao.length === 0) {
        observacao = null;
      }

      this.$axios
        .post("/configuracao", {
          turnos,
          salas,
          professores,
          id_configuracao: this.id,
          materias,
          observacao,
        })
        .then((response) => {
          if (response.status === 200 && reload) {
            this.load(true);
          }
        })
        .catch((err) => console.log(err));
    },

    getTurnoIdByName(nomeTurno) {
      return this.turnos.find((t) => t.name === nomeTurno).id;
    },

    deleteTurno(editedIndex) {
      this.changed();
      const { id } = this.turnos[editedIndex];
      this.turnos.splice(editedIndex, 1);
      this.salas = this.salas.filter((s) => s.turno !== id);
    },

    saveTurno(editedIndex, editedItem) {
      if (editedIndex === -1) {
        //Novo turno
        editedItem.id = uuidv4();
        this.turnos.push(editedItem);
        this.changed();
      } else {
        this.checkChanges(this.turnos[editedIndex], editedItem);
        Object.assign(this.turnos[editedIndex], editedItem);
      }
    },

    deleteSala(nomeTurno, nomeSala) {
      this.changed();
      this.salas = this.salas.filter(
        (sala) => !(sala.turno === nomeTurno && sala.name === nomeSala)
      );
    },

    saveSala(nomeSala, sala) {
      if (nomeSala === undefined) {
        this.changed();
        this.salas.push(sala);
      } else {
        const salaIndex = this.salas.findIndex((s) => s.name === nomeSala);
        this.checkChanges(this.salas[salaIndex], sala);
        this.salas.splice(salaIndex, 1, sala);
      }
    },

    deleteProfessor(editedIndex) {
      const nomeProfessor = this.professores[editedIndex].name;
      this.professores.splice(editedIndex, 1);
      this.changed();

      this.turnos.forEach((turno) => {
        delete this.linhasAulas[turno.name][nomeProfessor];
        delete this.linhasMaterias[turno.name][nomeProfessor];
      });
    },

    saveProfessor(editedIndex, editedItem) {
      if (editedIndex === -1) {
        //Novo professor
        editedItem.uuid = uuidv4();
        this.professores.push(editedItem);
        this.changed();
      } else {
        this.checkChanges(this.professores[editedIndex], editedItem);
        Object.assign(this.professores[editedIndex], editedItem);
      }
    },

    deleteMateria(editedIndex) {
      this.materias.splice(editedIndex, 1);
      this.changed();
    },

    saveMateria(editedIndex, editedItem) {
      if (editedIndex === -1) {
        editedItem.uuid = uuidv4();
        this.materias.push(editedItem);
        this.changed();
      } else {
        this.checkChanges(this.materias[editedIndex], editedItem);
        Object.assign(this.materias[editedIndex], editedItem);
      }
    },
  },
};
</script>

